     </div>\n            </div>\n        </slick-carousel>\n    </div>\n</template>\n\n<script>\n    export default {\n        props: {\n            'shimmerCount': {\n                default: 6,\n            },\n            localeDirection: {\n                String,\n                default: 'rtl'\n            }\n        },\n\n        data: function () {\n            return {\n                shimmerCountInt: parseInt(this.shimmerCount),\n                sliderSetting: {}\n            }\n        },\n        mounted: function () {\n            this.sliderSetting = {\n                \"dots\": false,\n                \"arrows\": false,\n                \"autoplay\": false,\n                \"speed\": 1000,\n                \"rtl\":(this.localeDirection === 'rtl'),\n                \"slidesToShow\": 5,\n                \"slidesToScroll\": 1,\n                \"responsive\": [\n                    {\n                        \"breakpoint\": 1200,\n                        \"settings\": {\n                            \"slidesToShow\": 5\n                        }\n                    },\n                    {\n                        \"breakpoint\": 992,\n                        \"settings\": {\n                            \"slidesToShow\": 4\n                        }\n                    },\n                    {\n                        \"breakpoint\": 768,\n                        \"settings\": {\n                            \"slidesToShow\": 3\n                        }\n                    },\n                    {\n                        \"breakpoint\": 600,\n                        \"settings\": {\n                            \"slidesToShow\": 2\n                        }\n                    },\n                    {\n                        \"breakpoint\": 480,\n                        \"settings\": {\n                            \"slidesToShow\": 1\n                        }\n                    }\n                ]\n            }\n        }\n    }\n</script>\n\n<style>\n    .shimmer-card-container {\n        width: 100%;\n    }\n\n    .shimmer-card {\n        width: 100%;\n        margin: 10px;\n        padding: 30px 40px;\n        border: 2px solid #fff;\n        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);\n    }\n\n    .shimmer-product-image {\n        width: 100%;\n        height: 180px;\n    }\n\n    .comment {\n        height: 10px;\n        background: #777;\n        margin-top: 20px;\n    }\n\n    .shimmer-wrapper {\n        width: 0px;\n        animation: fullView 0.5s forwards linear;\n    }\n\n    @keyframes fullView {\n        100% {\n            width: 100%;\n        }\n    }\n\n    .animate {\n        animation : shimmer 2s infinite;\n        background: linear-gradient(to right, #eff1f3 4%, #e2e2e2 25%, #eff1f3 36%);\n        background-size: 1000px 100%;\n    }\n\n    @keyframes shimmer {\n        0% {\n            background-position: -1000px 0;\n        }\n        100% {\n            background-position: 1000px 0;\n        }\n    }\n</style>\n"],"sourceRoot":""}]);
